//HEADER BANNER

/*Заголовок баннера*/

.banner__title {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	margin-top: 90px;
	height: 500px;
	position: relative;

	@include bp-768 {
		height: 400px;
	}
	@include bp-640 {
		height: 350px;
	}
	@include bp-480 {
		margin-top: 30px;
		height: 290px;
	}
}

.main-title {
	h1 {
		z-index: 1;
		font-size: 32px;
		font-weight: bold;
		line-height: 1.3;
		text-align: center;
		margin-bottom: 20px;
		@include bp-640 {
			font-size: 26px;
		}
		@include bp-480 {
			font-size: 22px;
			margin-bottom: 10px;
		}
	}
	p {
		z-index: 1;
		font-size: 20px;
		line-height: 1.2;
		text-align: center;
		@include bp-640 {
			font-size: 16px;
		}
	}
}


/*-----------*/


/*Картинка*/

.banner-img {
	width: 100%;
	height: 600px;
	img {
		position: absolute;
		z-index: -1;
		bottom: 0;
		max-width: 100%;
    	height: auto;
    	width: auto\9; /* ie8 */
		left: 50%;
		-webkit-transform: translateX(-50%);;
		transform: translateX(-50%);
	}
}

/*-----------*/


// HEADER END ==========



