//FOOTER

/*Общее*/

footer {
	background-color: #FAFAFA;
}

.footer__container {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	padding-top: 20px;
	padding-bottom: 20px;
	@include bp-768 {
		flex-direction: column-reverse;
	}
}

/*-----------*/


/*Левая часть*/

.footer__left {
	display: flex;
	flex-direction: row;
	align-items: center;
	flex: 1;
}

/*-----------*/


/*Правая часть*/

.footer__right {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	flex: 1;
	@include bp-640 {
		flex-direction: column-reverse;
	}
}

.contact__mail {
	position: relative;
	margin: 10px 15px;
	width: 110px;
	&::before {
		position: absolute;
		left: 0;
		top: 5px;
		content: '';
		background: url('./../img/ic-mail.png') no-repeat;
		width: 14px;
		height: 10px;
	}
	a {
		padding-left: 20px;
		font-size: 13px;
		color: $link;
	}
}

.contact__address {
	position: relative;
	margin: 10px 15px;
	width: 250px;
	&::before {
		position: absolute;
		left: 0;
		top: 6px;
		content: '';
		background: url('./../img/ic-map.png') no-repeat;
		width: 12px;
		height: 17px;
	}
	p {
		padding-left: 20px;
		font-size: 13px;
		font-family: "Roboto", Arial;
		color: rgb(0, 103, 90);
		line-height: 1.2;
	}
}

.contact__phone {
	position: relative;
	margin: 10px 15px;
	width: 150px;
	&::before {
		position: absolute;
		left: 0;
		top: 0;
		content: '';
		background: url('./../img/ic-phone.png') no-repeat;
		width: 10px;
		height: 17px;
	}
	p {
		padding-left: 20px;
		font-size: 15px;
		color: #00A08C;
	}
}


.copyright {
	border-top: 1px solid #E7ECEB;
	text-align: center;
	padding-top: 8px;
	padding-bottom: 8px;
	span {
		font-size: 12px;
		color: rgb(186, 204, 200);
	}
}

/*-----------*/


