@charset "UTF-8";

/**
 * Основные стили
 **/

*,
*::before,
*::after {
	box-sizing: border-box;
} 

html {
	font-family: sans-serif;
	line-height: 1.2;
}

body {
	margin: 0;
	font-size: 1rem;
  	font-weight: 400;
	font-family: 'Roboto', 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

h1, h2, h3, h4, h5, h6, p {
	margin-top: 0;
	margin-bottom: 0;
	color: #003b34;
}

h1 {
	font-size: 2.5rem;
  }
  
h2 {
	font-size: 2rem;
}
  
h3 {
	font-size: 1.75rem;
}
  
h4 {
	font-size: 1.5rem;
}
  
h5 {
	font-size: 1.25rem;
}

h6 {
	font-size: 1rem;
}

p {
	margin-top: 0;
  	margin-bottom: 0;
}

a {
	color: #007bff;
	text-decoration: none;
	background-color: transparent;
}

a:hover {
	color: #0056b3;
	text-decoration: underline;
}

a:not([href]) {
	color: inherit;
	text-decoration: none;
}

a:not([href]):hover {
	color: inherit;
	text-decoration: none;
}

img {
	vertical-align: middle;
	border-style: none;
}

svg {
	overflow: hidden;
	vertical-align: middle;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

table {
	border-collapse: collapse;
}

th {
	text-align: inherit;
}

table, th, td {
	border: 1px solid black;
}

caption {
	padding-top: 0.75rem;
	padding-bottom: 0.75rem;
	color: #6c757d;
	text-align: left;
	caption-side: bottom;
}

button:focus {
	outline: 55px dotted;
	outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
	margin: 0;
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

button:not(:disabled),
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled) {
	cursor: pointer;
}

.container {
	@include min-max(320px, 100%, 0);
}

.wrap {
	@include min-max(320px, 1200px, 15px);
}




// Отступы сверху и снизу для блоков LP

.padding-block {
	padding-top: 80px;
	padding-bottom: 80px;
}

@media (max-width: 992px) { 
	.padding-block {
		padding-top: 60px;
		padding-bottom: 60px;
	}
}

@media (max-width: 480px) { 
	.padding-block {
		padding-top: 30px;
		padding-bottom: 30px;
	}
}