//CATALOG

/*Карточки*/

.catalog__container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	@include bp-768 {
		flex-direction: column;
	}
}

.catalog__item {
	flex: 1;
	width: 360px;
	height: 420px;
	margin: 15px;
	border-radius: 10px;
	background-color: #4DB2A8;
	transition-duration: 0.3s;
	@include bp-768 {
		width: auto;
		height: 150px;
	}
	a {
		width: 100%;
	}
}

.catalog__item-title {
	display: inline-block;
	color: $white;
	width: 100%;
	height: 100%;
	padding: 40px 30px;
	overflow: hidden;
	span {
		color: #ccfffa;
	}
	h4 {
		margin-top: 10px;
		color: $white;
	}
	@include bp-768 {
		padding: 20px 20px;
	}

}

.catalog__item:hover {
	box-shadow: 0 0 35px rgb(167, 235, 229);
}

.catalog-bg__one {
	background: url('./../img/category-one.png') no-repeat;
	background-size: cover;
}
.catalog-bg__two {
	background: url('./../img/category-two.png') no-repeat;
	background-size: cover;
}
.catalog-bg__three {
	background: url('./../img/category-three.png') no-repeat;
	background-size: cover;
}

