.hamburg__menu {
  background-color: #8CCFC9;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  display: table;
  text-align: center;
  opacity: 0;
  transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform: scale(0); }
  .hamburg__menu.act {
    opacity: 1;
    transform: scale(1); }
    .hamburg__menu.act ul li {
      opacity: 1;
      transform: translateX(0); }
  .hamburg__menu ul {
    display: table-cell;
    vertical-align: middle;
    padding-left: 0; }
  .hamburg__menu li {
    list-style: none;
    transition: all 400ms 510ms;
    opacity: 0; }
    .hamburg__menu li:nth-child(odd) {
      transform: translateX(30%); }
    .hamburg__menu li:nth-child(even) {
      transform: translateX(-30%); }
    .hamburg__menu li:last-child {
      transform: none; }
  .hamburg__menu a {
