//HAMBURGER MENU

/*Кнопка меню*/

.header__hamburg {
	display: none;
	position: relative;
	width: 35px;
	height: 35px;
	@include bp-992 {
		display: block;
	}
}

.menuBtn {
	display: block;
	position: absolute;
	z-index: 150;
	top: 4px;
	right: 0px;
	width: 35px;
	height: 35px;
}
  
.menuBtn span,
.menuBtn::before,
.menuBtn::after {
	display: block;
	position: absolute;
	width: 35px;
	height: 2px;
	background: #00a08c;
	-webkit-transition: all .2s ease-in-out;
	-moz-transition: all .2s ease-in-out;
	-ms-transition: all .2s ease-in-out;
	-o-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
}
  
.menuBtn span {
	top: 50%;
	margin-top: -5px;
}
  
.menuBtn::before,
.menuBtn::after {
	content: "";
}
  
.menuBtn::before {
	top: 0px;
}
  
.menuBtn::after {
	bottom: 8px;
}
  
.menuBtn.type1.close span {
	opacity: 0;
}
  
.menuBtn.type1.close::before {
	top: 13px;
	-moz-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}
  
.menuBtn.type1.close::after {
	bottom: 20px;
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

/*-----------*/


/*Выпадающее меню*/

.hamburg__menu {
	background-color: #8CCFC9;
	position: absolute;
	right: 0;
	top: 0;
	z-index: 100;
	height: 100%;
	width: 100%;
	display: table;
	text-align: center;
	opacity: 0;
	transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	transform: scale(0);
	&.act {
		opacity: 1;
		transform: scale(1);
		ul li {
		opacity: 1;
		transform: translateX(0);
		}
	}

	ul {
		display: table-cell;
		vertical-align: middle;
		padding-left: 0;
	}

	li {
		list-style: none;
		transition: all 400ms 510ms;
		opacity: 0;
		&:nth-child(odd) {
			transform: translateX(30%);
		}
		&:nth-child(even) {
			transform: translateX(-30%);
		}
		&:last-child {
			transform: none;
		}
	}
	a {
		color: $white;
		display: inline-block;
		padding: 8px 5px;
		width: 100%;
		font-size: 26px;
		&.suBtn {
			font-weight: 700;
			margin-top: 20px;
			padding: 10px 35px;
			font-size: 20px ;
			background-color: #00a08c;
		}
	}

}

.stop-scroll {
	overflow: hidden;
}


