//INFO ABOUT

/*Календарь*/

.info__container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	@include bp-992 {
		flex-direction: column;
	}
}

.info__decade {
	border-left: 1px solid #C6D8D6;
}

.info__decade,
.info__about,
.info__news {
	flex: 1 1 360px;
	margin: 15px;
	@include bp-992 {
		flex-basis: auto;
	}
}

.date__item {
	display: flex;
	flex-direction: row;
	margin-bottom: 25px;
}


.date__year {
	position: relative;
	span {
		font-size: 12px;
		margin-right: 15px;
		padding-left: 15px;
		color: rgb(142, 165, 162);
	}
	&::before {
		position: absolute;
		content: '';
		left: -5px;
		top: 5px;
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background-color: #C6D8D6;
	}
}

.date__text {
	p {
		font-size: 13px;
		font-family: "Roboto";
		color: rgb(0, 59, 52);
		line-height: 1.385;
	}
}

/*-----------*/


/*Описание компании*/

.info__about {
	h5 {
		margin-bottom: 10px;
	}
	p {
		font-size: 13px;
		line-height: 1.5;
	}
}



/*-----------*/


/*Новости*/

.info__news {
	h5 {
		margin-bottom: 10px;
	}
}

.news__block {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
	@include bp-992 {
		flex-direction: row;
	}
	@include bp-480 {
		flex-direction: column;
	}
}

.news__item {
	display: flex;
	flex-direction: column;
	margin: 10px 0;
	
	a {
		font-size: 14px;
		line-height: 1.4;
		color: $link;
		float: left;
		margin-bottom: 5px;
	}
	span {
		font-size: 12px;
		color: rgb(116, 151, 147);
		font-weight: 100;
	}
	@include bp-992 {
		margin: 10px 10px;
	}
	@include bp-480 {
		margin: 10px 0;
	}
}

.news__btn {
	display: inline-block;
	font-size: 13px;
	color: rgb(116, 151, 147);
	border-width: 1px;
	border-color: rgb(187, 204, 202);
	border-style: solid;
	border-radius: 3px;
	padding: 6px 12px;
	transition-duration: 0.3s;
	&:hover {
		background-color: rgb(233, 245, 243);
		border-color: rgb(233, 245, 243);
		color: #00a08c;
		text-decoration: none;
	}
}


/*-----------*/


