//NUMBERS BLOCK

/*Цифры*/

.numbers__wrap {
	display: flex;
	justify-content: center;
}

.numbers__container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 800px;
	@include bp-480 {
		flex-wrap: wrap;
		justify-content: center;
	}
}

.number__item {
	text-align: center;
	margin: 15px;
	p {
		font-size: 100px;
		color: rgb(139, 206, 200);
		font-weight: bold;
		text-align: center;
		@include bp-768 {
			font-size: 40px;
		}
	}
	span {
		font-size: 14px;
		color: rgb(139, 206, 200);
		text-transform: uppercase;
		line-height: 1.3;
		text-align: center;
	}
}

