//HEADER

/*Левая часть шапки*/

.header__wrap {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;
	justify-content: space-between;
	padding-top: 15px;
	padding-bottom: 15px;
}

.header__left {
	display: flex;
	flex-direction: row;
	align-items: center;
	flex: 1 1 480px;
}

.header__logo {
	margin-right: 40px;
}

.header__descript {
	display: flex;
	align-items: center;
	@include bp-640 {
		display: none;
	}
	p {
		font-size: 13px;
		font-family: "Roboto";
		color: rgb(0, 105, 92);
		line-height: 1.2;
		width: 216px;
	}
}

/*-----------*/


/*Правая часть шапки*/

.header__right {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-end;
	flex: 1 1 400px;

}

.main__menu {
	@include bp-992 {
		display: none;
	}
	ul {
		display: flex;
		justify-content: flex-end;
	}
	li {
		list-style: none;
		a {
			display: inline-block;
			padding: 15px;
			color: $text-light;
			font-weight: 100;
			border-bottom: 1px solid transparent;

			transition-duration: 0.3s;
			&:hover {
				color: $link;
				border-bottom: 1px solid $link;
				text-decoration: none;
			}
		}
	}
}

/*-----------*/


// HEADER END ==========


